<template>
  <div id="BaseHeader">
    {{msg}}
  </div>
</template>

<script>
export default {
  name: 'BaseHeader',
  data () {
    return {
      msg: '京东'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #BaseHeader{
    width: 100%;
    height: 20%;
    background-color: red;
  }
</style>



